/* src/components/Purchase/OrderSummary.css */
.order-summary {
    position: fixed;
    top: 8rem;               /* Sayfanın üstünden boşluk (Navbar’dan sonra) */
    right: 2rem;             /* Sağdan boşluk */
    width: 250px;            /* Genişlik */
    padding: 1.5rem;
    background-color: rgba(255, 255, 255, 0.9);
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.order-summary h3 {
    text-align: center;
    color: #333;
    margin-bottom: 1rem;
}

.order-items {
    list-style: none;
    padding: 0;
    margin: 0;
}

.order-item {
    display: flex;
    justify-content: space-between;
    margin-bottom: 0.5rem;
    font-size: 1rem;
    color: #555;
}

.order-total {
    display: flex;
    justify-content: space-between;
    font-weight: bold;
    font-size: 1.1rem;
    margin-top: 1rem;
    color: #333;
}
