.product-details-container {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    margin: 6rem auto;
    max-width: 1200px;
    padding: 2rem;
    background: #fff;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

@media (min-width: 768px) {
    .product-details-container {
        flex-direction: row;
        align-items: flex-start;
    }
}

.product-image-section {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
}

.product-details-image {
    width: 100%;
    max-width: 400px;
    height: auto;
    border-radius: 10px;
    object-fit: cover;
}

.stock-rating {
    display: flex;
    justify-content: space-between;
    width: 100%;
    max-width: 400px;
}

.wishlist-button {
    background-color: #f5f5f5;
    color: #333;
    border: none;
    padding: 10px;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.wishlist-button:hover {
    background-color: #e1e1e1;
}

.product-details-info {
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.product-details-info h1 {
    font-size: 2.5rem;
    margin-bottom: 0.5rem;
    color: #333;
}

.product-details-info p {
    font-size: 1.125rem;
    color: #555;
    line-height: 1.6;
}

.product-details-info .price {
    font-size: 1.5rem;
    font-weight: bold;
    color: #007bff;
    margin: 1rem 0;
}

.add-to-cart-container {
    display: flex;
    gap: 1rem;
    align-items: center;
}

.quantity-selector {
    display: flex;
    align-items: center;
    gap: 0.5rem;
}

.quantity-selector button {
    background-color: #ddd;
    border: none;
    padding: 0.5rem;
    border-radius: 5px;
    cursor: pointer;
}

.quantity-selector input {
    width: 3rem;
    text-align: center;
    border: 1px solid #ccc;
    border-radius: 5px;
}

.add-to-cart-button {
    background-color: #007bff;
    color: #fff;
    padding: 0.75rem 1.5rem;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1rem;
    transition: background-color 0.3s ease;
}

.add-to-cart-button:hover {
    background-color: #0056b3;
}

.comments-section {
    margin: 2rem auto;
    max-width: 800px;
    padding: 2rem;
    background: #f9f9f9;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.comments-section h2 {
    font-size: 1.8rem;
    margin-bottom: 1rem;
    color: #333;
}

.comments-section p {
    font-size: 1rem;
    color: #666;
}
.stock-rating-wishlist {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    max-width: 400px;
    margin-top: 1rem;
}

.stock-rating-wishlist p {
    margin: 0;
    font-size: 1rem;
    color: #555;
}

.wishlist-icon {
    background-color: transparent;
    border: none;
    font-size: 2.5rem; /* Increase the size of the heart */
    color: #ff6f61; /* A vibrant red color */
    cursor: pointer;
    transition: transform 0.3s ease-in-out, color 0.3s ease-in-out; /* Add smooth transitions */
}

.wishlist-icon:hover {
    transform: scale(1.4); /* Scale up the heart on hover */
    color: #ff4d4d; /* Make the color brighter on hover */
    text-shadow: 0 0 10px #ff4d4d; /* Add a glowing effect */
}

.wishlist-icon:active {
    transform: scale(1.2); /* Slightly shrink on click for a "pressed" effect */
}

