/* src/components/Purchase/Purchase.css */
.purchase-container {
    display: flex;
    gap: 2rem;
    max-width: 800px;
    margin: 8rem auto 0 auto;
    padding: 2rem;
    background-color: rgba(255, 255, 255, 0.9);
    border-radius: 10px;
    box-shadow: 0 8px 15px rgba(0, 0, 0, 0.2);
}

.purchase-form h2 {
    text-align: left;           /* Başlık sola yaslı */
    color: #333;
    margin-bottom: 1rem;
}

.form-group {
    display: flex;
    flex-direction: column;
    margin-bottom: 1rem;
}

.form-group label {
    margin-bottom: 0.5rem;
    color: #555;
}

.form-group input {
    padding: 0.5rem;
    border-radius: 5px;
    border: 1px solid #ddd;
}

.form-row {
    display: flex;
    gap: 1rem;
}

.purchase-button {
    width: 100%;
    padding: 0.75rem;
    border: none;
    border-radius: 5px;
    background-color: #4CAF50;
    color: white;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.purchase-button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
}

.purchase-button:hover:not(:disabled) {
    background-color: #45a049;
}

.credit-card {
    margin-left: auto; /* Sağda konumlandırıyoruz */
}
